import React, { ReactElement } from "react";
import * as ReportingTypeAPI from "../api/reporting-types";
import {
  ReportingType,
  ReportingTypeDTO,
} from "../api/reporting-types/model.interface";
import { Grid } from "@material-ui/core";
import withPageLayout from "../hocs/withPageLayout";
import { PageHeaderContext } from "../context/layout";
import AppBtn from "../components/form/AppBtn";
import AppInput from "../components/form/AppInput";
import { withRouter } from "react-router";

const ReportingTypeAdd = (props: any) => {
  const LayoutContext = React.useContext(PageHeaderContext);

  const [name, setName] = React.useState("");
  const [time, setTime] = React.useState("");

  const addReportingType = async () => {
    props.setShowLoading(true);

    const reportingTypeDTO: ReportingTypeDTO = {
      name,
      time,
      school_id: 1,
    };
    const addedREportingType = await ReportingTypeAPI.post(reportingTypeDTO);
    props.setShowLoading(false);

    typeof props.history.goBack === "function" && props.history.goBack();
  };

  React.useEffect(() => {
    LayoutContext.setTitle("Add Reporting Type");
  }, []);

  return (
    <>
      <Grid>
        <AppInput
          label="Name"
          value={name}
          placeholder="Enter Name"
          prefix={null}
          suffix={null}
          onChangeText={(name: string) => setName(name)}
        />
        <AppInput
          label="Time"
          value={time}
          type="time"
          placeholder="Enter Time"
          prefix={null}
          suffix={null}
          onChangeText={(colorCode: string) => setTime(colorCode)}
        />
        <AppBtn block title="Save" onClick={addReportingType} />
      </Grid>
    </>
  );
};
export default withRouter(withPageLayout(ReportingTypeAdd));
