import api from "../index";
import { ReportingTypeDTO } from "./model.interface";

export const get = async () => await api.get("reporting-types");
export const details = async (id: number) =>
  await api.get(`reporting-types/${id}`);

export const post = async (reportingType: ReportingTypeDTO) =>
  await api.post("reporting-types", reportingType);
export const put = async (id: number, reportingType: ReportingTypeDTO) =>
  await api.put(`reporting-types/${id}`, reportingType);
